body {
    font-family: Montserrat, sans-serif;
    font-size: 18px;
    font-style: normal;
    letter-spacing: 0.08em;
    background-color: var(--primary-bg-color);
    color: var(--primary-text-color);
}

a {
    text-decoration: none;
    color: inherit;
}

ul {
    padding: 0;
    margin: 0;

    list-style: none;
}

h1,
h2,
h3,
h4,
h5,
h6,
p,
ul {
    margin: 0;
}

img {
    display: block;
    max-width: 100%;
    height: auto;
}

a,
button {
    cursor: pointer;
}